<template>
  <v-container class="bg-white">
    <v-banner single-line>
      <h2 class="h5"><span class="cblu bbred">All Jobs in {{city}}</span></h2>
    </v-banner>
    <v-row class="py-0 py-md-5">
      <v-col cols="6" md="2" v-for="(job,i) in jobscatcity" :key="i">
        <v-card>
          <router-link :to="{name: 'jobsbycatcity',params: { job_cat_slug: job.job_cat_slug , city: job.city }}">
            <v-card-text class="text-center h6">
              <span class="me-1"><img :src="(url+job.job_img_url)" :alt="job.job_cat_name" width="32" class="me-1">{{job.job_cat_name}}</span>
              <span class="text--white badge bg-dark">{{job.jobcount}}</span>
            </v-card-text>
          </router-link>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "AllCategoryjobs",
  props: {
    city: String
  },
  data (){
    return{
      url:'https://bizlx.itechvision.in',
      jobscatcity:[],
    }
  },
  mounted() {
    this.getCatbycity();
  },
  methods:{
    getCatbycity(){
      axios.get(`jobcategory/bycity/${this.city}`)
          .then((resp) =>{
            console.log(resp.data);
            this.jobscatcity = resp.data.jobcatbycity;
          })
    }
  }
}
</script>

<style scoped>
.v-banner__text {
  display: flex;
  }
</style>